::-webkit-scrollbar{
    width: 8px;
    height: auto;

}
::-webkit-scrollbar-track{
    box-shadow: inset 0 0 5px rgb(187, 162, 237);
}
::-webkit-scrollbar-thumb{
    background-color: green;
    border-radius: 10%;
}
::-webkit-scrollbar-thumb:hover{
    background-color: blueviolet;
}
@media (max-width: 600px) {
    .scroll-to-top {
      display: none;
    }
  
  }

